@import "../../styles/utils";

.simple-nav {
  $nav-size: 40px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: $nav-size;

  &__button {
    @include square($ui-main-button-size);
    @include normalize-background;
    @include noBorder;

    position: absolute;
    background-color: #fdfdfd;
    border-radius: 50%;
    box-shadow: 0 0 4px 2px rgba(221,221,221,1);
    background-size: 40%;
    transform: translate(0, ($nav-size / 2) + $ui-block-padding);

    &_previous {
      @include square($ui-additional-button-size);

      transform: translate(-(($ui-main-button-size / 2) + ($ui-additional-button-size / 2) + 10px), ($nav-size / 2) + $ui-block-padding);
      background-image: url("../../../public/assets/user/navigation/back.png");
    }

    &_next {
      background-image: url("../../../public/assets/user/navigation/next.png");
    }
  }
}
